.addkeywords{
    font-family: Arial, sans-serif;
    text-align: center;
    position: absolute;
    width: 10%;
    min-width: 170px;
    top: 150px;
    right: min(300px, 15vw);
    height:42px;
    z-index: 2;  /* high z-index to ensure it appears on top */
}
.addkeywordsbtn{
    text-align: center;
    padding: 10px;
    border-radius: 15px;
    border: 3px solid beige;
    cursor:pointer;
    width:175px;
    height:42px;
    font-weight:900;
    color:cornflowerblue;
    font-size:0.9rem;
}
.addkeywordsbtn:hover{
    background-color: antiquewhite;
}
.popup{
    user-select: none; /* standard syntax */
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE/Edge */
    position:fixed;
    top: 42.5%;
    left:37.5%;
    background-color: #D5FFFF70;
    width: 25%;
    min-height:15%;
    border-radius: 20px;
    border: 1rem solid lightblue;
    opacity: 0;
    transition:1s;
    z-index:-20;
}
.popup.show{
    transition: 1s;
    opacity: 1;
    z-index:1002;
}
.popuptitle{
    position:relative;
    top:15px;
    left:34%;
    font-weight: 800;
    font-size: larger;
}
.backdrop{
    position:fixed;
    top:0px;
    left:0px;
    z-index:-19;
    height:100%;
    width:100%;
    background: rgba(0,0,0,0.2);
    opacity: 0;
}
.backdrop.show{
    opacity: 1;
    z-index: 1000;
}
.closebutton{
    position:fixed;
    font-size: 2rem;
    top:42.5%;
    left:37.5%;
    z-index:1100;
    font-weight: 1000;
}
.option.pop{
    border-radius: 0px;
    background-color: rgba(255, 255, 255, 0.5)
}
.popupoptionslist{
    margin-top:100px;
}
.option.keywordinput{
    width:75%;
}
.option.addkeywordsbtn{
    width:15%;
    font-weight: 2000;
    font-size: 1rem;
}
.optionsname{
    width:60%;
    display:inline-grid;
}
.deletebtn{
    position:relative;
    width:35%;
    display:inline-grid;
}
.block{
    overflow-y:auto;
    max-height: 200px;
}